<template>
  <div id="dashboard">
   <ul class="collection with-header">
     <li class="collection-header"><h4>Employees manger</h4>
     </li>
     <li v-for="employee in Employees"
     v-bind:key="employee.id"
     class="collection-item" >
   <div class="chip">{{employee.dept}}</div>
   {{employee.employee_Id}}:{{employee.name}}
  <router-link class="secondary-content"
  v-bind:to="{name: 'view-employee', params: {employee_id: employee.employee_Id}}">
  <i class="fa fa-eye"></i>
  </router-link>
    </li>
   </ul>

    <div class="fixed-action-btn">
   <router-link class="btn-floating btn-large red" v-bind:to="{name: 'new-employee'}">
      <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>
<script>
import db from './firebaseInit'
export default {
  name: 'dashboard',
  data() {
    return {
    Employees: []
    }
  },
  created(){
db.collection('Employees').orderBy('dept').get().then(querySnapshot => {
  querySnapshot.forEach(doc => {
    const data = {
       'id': doc.id,
       'employee_Id': doc.data().employee_Id,
       'name': doc.data().name,
       'dept': doc.data().dept,
       'position': doc.data().position
    }
    console.log(doc.employee_id)
    this.Employees.push(data)
  })
})
  }

}
</script>>



